    width: 20vw;
    height: 40px;
    background-color: var(--tertiary-light-blue);
    border-radius: 50px;
    cursor: pointer;
    user-select: none;
    box-shadow: inset 0 0 5px var(--main-blue);
    transition: background-color 0.3s ease;
  }

  .unit-switch label .unit-C,
  .unit-switch label .unit-F {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-weight: bold;
    z-index: 2;
    width: 50%;
    text-align: center;
    pointer-events: none;
    color: var(--main-blue);
  }
  .unit-switch label .unit-F{
    color: var(--neutral-offwhite);
  }

  .unit-switch label .unit-C {
    left: 0;
  }

  .unit-switch label .unit-F {
    right: 0;
  }

  .unit-switch label .slider {
    position: absolute;
    top: 3px;
    left: 3px;
    width: 34px;
    height: 34px;
    background-color: var(--tertiary-light-yellow);
    border-radius: 50px;
    transition: 0.3s;
  }

  /* Toggle when checked */
  .unit-switch input:checked + label .slider {
    transform: translateX(32px);
    background-color: var(--tertiary-light-yellow);
  }

  .unit-switch input:checked + label .unit-C {
    color: var(--neutral-offwhite);
  }

  .unit-switch input:checked + label .unit-F {
    color: var(--main-blue);
  }

  .next-seven-days{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .day-mini-forecast {
    width: 70%;
    height: 15vh;
    margin: auto;
    padding-bottom: 10%;
  }
  .day-mini-forecast:hover{
    transform: translateY(-5%);
  }
  .day-mini-forecast img{
    width: 30%;
    height: 30%;
  }

  .forecasts {
    gap: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .info-box-container {
    gap: 0.5rem;
    margin-left: -1rem;
    margin-bottom: 2rem;
  }

  .info-box h3 {
    font-size: 1.5rem;
  }

  .info-box span {
    font-size: 1rem;
  }
}

@media (min-width: 361px) and (max-width: 500px) {
  .unit-switch input:checked + label .slider{
    transform: translateX(40px);
  }

  .info-box-container {
    gap: 0.5rem;
    margin-right: 1.2rem;
    margin-left: -0.5rem;
  }

  .info-box h3 span{
    scale: 1;
  }
}
